import "../styles/components/Header.css";

import logoWithTypoImgUrl from "../assets/logo_with_typo.png";
import onlyTypoImgUrl from "../assets/logo_typo.png";

import userPfpImgUrl from "../assets/basic_user_pfp_img.png";

import { Link } from "react-router-dom";

/**
 * pageType   header element return
 * @param {string} pageType - main, item
 * @returns {element} - header element
 */
function Header({ pageType = "main", isLogin = false }) {
  const buttonClassName = `blue-button login-link-button ${pageType}`;
  return (
    <header>
      <div className="header-left-container">
        <Link to="/">
          <img
            className="header-logo-img"
            src={logoWithTypoImgUrl}
            alt=" "
          />
          <img
            className="header-typo-img"
            src={onlyTypoImgUrl}
            alt=" "
          />
        </Link>
        {pageType === "item" && (
          <div className="item-page-header-nav">
            <div className="free-board-link"></div>
            <div className="used-market-link active"></div>
          </div>
        )}
      </div>
      {isLogin ? (
        <img
          className="user-pfp-img"
          src={userPfpImgUrl}
          alt="  "
        />
      ) : (
        <Link to="/login">
          <button className={buttonClassName}></button>
        </Link>
      )}
    </header>
  );
}

export default Header;
